import wPicker from "@/components/w-picker/w-picker.vue";
export default {
	components: {
		wPicker
	},
	data() {
		return {
			columns: [],
			clubName: '请选择俱乐部',
			clubId: '',
			money: ''
		}
	},
	created() {
		this.columns = [{label:"来虎",value:"1"},{label:"波利",value:"2"},{label:"晴天",value:"3"}]
	},
	methods: {
		choiceClub() {
			this.$refs.picker1.show()
		},
		onConfirm(v) {
			// console.log(v)
			this.clubId = v.checkArr.value
			this.clubName = v.checkArr.label
		},
		inputMoney(v) {
			this.money = v.detail
		},
		submit() {
			if(this.clubId == '') {
				uni.showToast({
					title: '请选择要充值的俱乐部',
					duration: 2000,
					icon: 'none'
				});
			}else if(Number(this.money) <= 0) {
				uni.showToast({
					title: '请输入您要充值的金额',
					duration: 2000,
					icon: 'none'
				});
			}else{
				const params = {
					clubId: this.clubId,
					money: this.money
				}
				console.log(params)
			}
		}
	}
}
